  resetValues() {
    this.othersViewModel.fuel = null;
    this.othersViewModel.kilometersPerLiter = undefined;
    this.othersViewModel.litersPer100Kilometers = undefined;
    this.othersViewModel.milesPerGallon = undefined;
  }

