  convertCurrent() {
    if (this.viewModel.current != null) {
      if (this.targetUnit === "Ampere (A)") {
        let data = UnitConverter.fromAmpere({
          current: this.viewModel.current,
        });
        this.viewModel.milliampere = data.milliampere;
        this.viewModel.microampere = data.microampere;
        this.viewModel.kiloampere = data.kiloampere;
        this.viewModel.megaampere = data.megaampere;
        this.viewModel.ampere = data.ampere;
        this.viewModel.current = data.current;
        // this.fromAmpere();
      } else if (this.targetUnit === "Milliampere (mA)") {
        // this.fromMilliampere();
        let data = UnitConverter.fromMilliampere({
          current: this.viewModel.current,
        });
        this.viewModel.milliampere = data.milliampere;
        this.viewModel.microampere = data.microampere;
        this.viewModel.kiloampere = data.kiloampere;
        this.viewModel.megaampere = data.megaampere;
        this.viewModel.ampere = data.ampere;
        this.viewModel.current = data.current;
      } else if (this.targetUnit === "Microampere (A)") {
        // this.fromMicroampere();
        let data = UnitConverter.fromMicroampere({
          current: this.viewModel.current,
        });
        this.viewModel.milliampere = data.milliampere;
        this.viewModel.microampere = data.microampere;
        this.viewModel.kiloampere = data.kiloampere;
        this.viewModel.megaampere = data.megaampere;
        this.viewModel.ampere = data.ampere;
        this.viewModel.current = data.current;
      } else if (this.targetUnit === "Kiloampere (kA)") {
        // this.fromKiloampere();
        let data = UnitConverter.fromKiloampere({
          current: this.viewModel.current,
        });
        this.viewModel.milliampere = data.milliampere;
        this.viewModel.microampere = data.microampere;
        this.viewModel.kiloampere = data.kiloampere;
        this.viewModel.megaampere = data.megaampere;
        this.viewModel.ampere = data.ampere;
        this.viewModel.current = data.current;
      } else if (this.targetUnit === "Megaampere (MA)") {
        // this.fromMegaampere();
        let data = UnitConverter.fromMegaampere({
          current: this.viewModel.current,
        });
        this.viewModel.milliampere = data.milliampere;
        this.viewModel.microampere = data.microampere;
        this.viewModel.kiloampere = data.kiloampere;
        this.viewModel.megaampere = data.megaampere;
        this.viewModel.ampere = data.ampere;
        this.viewModel.current = data.current;
      }
    }
  }

