        this.viewModel.cubicCentimeter = data?.cubicCentimeter;
        this.viewModel.cubicMillimeter = data?.cubicMillimeter;
        this.viewModel.cubicFeet = data?.cubicFeet;
        this.viewModel.cubicInch = data?.cubicInch;
        this.viewModel.liter = data?.liter;
        this.viewModel.milliliter = data?.milliliter;
        this.viewModel.usGallon = data?.usGallon;
        this.viewModel.ukGallon = data?.ukGallon;
        this.viewModel.cubicMeter = data?.cubicMeter;
