  selectActivityLevel(activityLevelName: string) {
    this.healthViewModel.waterIntakePropertiesObject.activityLevelName =
      activityLevelName;
    this.calculateWaterIntake();
  }
  selectclimateFactor(climateFactorName: string) {
    this.healthViewModel.waterIntakePropertiesObject.climateFactorName =
      climateFactorName;
    this.calculateWaterIntake();
  }
  calculateWaterIntake() {
    let dataFromWaterIntake = UnitConverter.calculateWaterIntake({
      weight: this.healthViewModel.waterIntakePropertiesObject.weight,
      activityLevelName:
        this.healthViewModel.waterIntakePropertiesObject.activityLevelName,
      climateFactorName:
        this.healthViewModel.waterIntakePropertiesObject.climateFactorName,
    });
    if (dataFromWaterIntake) {
      this.healthViewModel.waterIntakePropertiesObject.waterIntake =
        dataFromWaterIntake.waterIntake;
    }
  }

