  calculateCompoundInterest() {
    let dataFromCI = UnitConverter.calculateCompoundInterest({
      compoundingFrequency:
        this.bankingViewmodel.compoundInterest.compoundingFrequency,
      timePeriod: this.bankingViewmodel.compoundInterest.timePeriod,
      timeUnit: this.targetUnit,
      rateOfInterest: this.bankingViewmodel.compoundInterest.rateOfInterest,
      principleAmount: this.bankingViewmodel.compoundInterest.principleAmount,
    });
    this.bankingViewmodel.compoundInterest.compoundInterest =
      dataFromCI?.Interest;
    this.bankingViewmodel.compoundInterest.totalAmount =
      dataFromCI?.TotalAmount;
  }

  resetValues() {
    this.bankingViewmodel.compoundInterest.principleAmount = undefined;
    this.bankingViewmodel.compoundInterest.rateOfInterest = undefined;
    this.bankingViewmodel.compoundInterest.timePeriod = undefined;
    this.bankingViewmodel.compoundInterest.compoundInterest = undefined;
    this.bankingViewmodel.compoundInterest.totalAmount = undefined;
    this.bankingViewmodel.compoundInterest.compoundingFrequency = "Monthly";
  }

