  workUnitNames = [
    "Joule (J)",
    "Kilojoule (kJ)",
    "Calorie (cal)",
    "Kilocalorie (kcal)",
    "Erg (erg)",
    "Watt-hour (Wh)",
  ];

  targetUnit: string = this.workUnitNames[0];

  // Function to convert work

  convertWork() {
    if (this.viewModel.work != null && this.viewModel.work > 0) {
      if (this.targetUnit === "Joule (J)") {
        // this.fromJoule();
        let data = UnitConverter.fromJoule({ work: this.viewModel.work });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else if (this.targetUnit === "Kilojoule (kJ)") {
        // this.fromKilojoule();
        let data = UnitConverter.fromKilojoule({ work: this.viewModel.work });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else if (this.targetUnit === "Calorie (cal)") {
        // this.fromCalorie();
        let data = UnitConverter.fromCalorie({ work: this.viewModel.work });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else if (this.targetUnit === "Kilocalorie (kcal)") {
        // this.fromKilocalorie();
        let data = UnitConverter.fromKilocalorie({
          work: this.viewModel.work,
        });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else if (this.targetUnit === "Erg (erg)") {
        // this.fromErg();
        let data = UnitConverter.fromErg({ work: this.viewModel.work });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else if (this.targetUnit === "Watt-hour (Wh)") {
        // this.fromWattHour();
        let data = UnitConverter.fromWattHour({ work: this.viewModel.work });
        this.viewModel.kilojoule = data?.kilojoule;
        this.viewModel.calorie = data?.calorie;
        this.viewModel.kilocalorie = data?.kilocalorie;
        this.viewModel.erg = data?.erg;
        this.viewModel.wattHour = data?.wattHour;
        this.viewModel.joule = data?.joule;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }
