import { Component, OnInit } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { commonConverterViewModel } from "src/app/models/view/common-converter.viewmodel";
import { CommonService } from "src/app/services/common.service";
import { CurrencyService } from "src/app/services/currency.service";
import { LogHandlerService } from "src/app/services/log-handler.service";
import Swal from "sweetalert2";
import { Plugins } from "@capacitor/core";

const { App } = Plugins;
@Component({
  selector: "app-currency",
  templateUrl: "./currency.component.html",
  styleUrls: ["./currency.component.scss"],
})
export class CurrencyComponent
  extends BaseComponent<commonConverterViewModel>
  implements OnInit
{
  targetUnit: string = "USD";
  constructor(
    private currencyService: CurrencyService,
    commonService: CommonService,
    logService: LogHandlerService
  ) {
    super(commonService, logService);
  }
  override viewModel: commonConverterViewModel = new commonConverterViewModel();

  ngOnInit(): void {
    this._commonService.searchInput = "";
    this._commonService.layoutVM.showHideClass = "hide";
    this._commonService.highlightCommonCategory = true;
    this._commonService.highlightOthersCategory = false;
    this._commonService.highlightEngineeringCategory = false;
    this._commonService.highlightBankingCategory = false;
    this._commonService.highlightHealthCategory = false;
    // fetchs currencies
    this.fetchCurrencyList();
    // /close the swal if user clicks back
    document.addEventListener("backbutton", () => {
      if (Swal.isVisible()) {
        Swal.close();
      }
    });
  }

  // func to convert currenty
  convertCurrency(): void {
    this.viewModel.searchInput = "";
    this.viewModel.convertedRatesArray = [];
    const target = this.getRate(this.targetUnit);

    if (target !== undefined) {
      this.viewModel.targetRates = target;
      this.viewModel.listOfRatesAndValues.forEach((currency) => {
        if (this.viewModel.currency != null) {
          const convertedRate =
            (1 / this.viewModel.targetRates) *
            currency.value *
            this.viewModel.currency;

          this.viewModel.convertedRatesArray.push({
            code: currency.code,
            value: convertedRate,
          });
          this.viewModel.filteredArray = [
            ...this.viewModel.convertedRatesArray,
          ];
        }
      });
    } else {
      console.error("Invalid target currency selected.");
    }
  }
  ///getting target currency  from user
  selectUnit(selectedCode: string): void {
    this.targetUnit = selectedCode;
    this.convertCurrency();
  }
  ///getting rates from listOfRates
  getRate(currencyCode: string): number | undefined {
    const currency = this.viewModel.listOfRatesAndValues.find(
      (c) => c.code === currencyCode
    );
    return currency ? currency.value : undefined;
  }

  resetValues(): void {
    this.viewModel.currency = null;
    this.viewModel.searchInput = "";
    this.targetUnit = "USD";
    this.viewModel.convertedAmounts = {};
    this.viewModel.filteredArray = [];
  }

  ///search
  onInputChange() {
    if (this.viewModel.searchInput.trim() !== "") {
      this.viewModel.filteredArray = this.viewModel.convertedRatesArray.filter(
        (item: any) => {
          return item.code
            .toLowerCase()
            .includes(this.viewModel.searchInput.toLowerCase());
        }
      );
    } else {
      this.viewModel.filteredArray = [...this.viewModel.convertedRatesArray];
    }
  }
  //fetching currency rates
  fetchCurrencyList() {
    this.currencyService.getAllCurrencyRates().subscribe((response: any) => {
      this.viewModel.currencyRatesObject = response;
      const timestamp = this.viewModel.currencyRatesObject.timestamp;
      const date = new Date(timestamp * 1000);
      this.viewModel.formattedDate = date.toLocaleString();
      this.simpleNotification();
      this.viewModel.listOfRatesAndValues = Object.entries(
        this.viewModel.currencyRatesObject.rates
      ).map(([code, value]) => ({
        code,
        value,
      }));
    });
  }

  //sweet alert

  simpleNotification() {
    const confirmationMessage = `Currency rates are sourced from OpenExchangeRates. Verify rates before using elsewhere. Last updated on: ${this.viewModel.formattedDate}`;
    Swal.fire({
      customClass: {
        confirmButton: "confirm-button-class",
        title: "title-class",
        icon: "icon-class",
      },
      icon: "warning",
      text: confirmationMessage,
      showCancelButton: false,
      showConfirmButton: true,
      confirmButtonText: "Ok",
    });
  }
}
