  convertArea() {
    if (
      this.viewModel.areaUnitPropertiesObject.areaInput != null &&
      this.viewModel.areaUnitPropertiesObject.areaInput > 0
    ) {
      if (
        this.viewModel.areaUnitPropertiesObject.targetUnit ===
        "Square meters (m)"
      ) {
        // this.fromSquareMeter();

        let dataFromSquareMeter = UnitConverter.fromSquareMeter({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromSquareMeter.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromSquareMeter.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromSquareMeter.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromSquareMeter.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromSquareMeter.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromSquareMeter.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromSquareMeter.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit ===
        "Square kilometers (km)"
      ) {
        // this.fromSquareKilometer();

        let dataFromSquareKilometer = UnitConverter.fromSquareKilometer({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre =
          dataFromSquareKilometer.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromSquareKilometer.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromSquareKilometer.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromSquareKilometer.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromSquareKilometer.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromSquareKilometer.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromSquareKilometer.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit ===
        "Square feet (ft)"
      ) {
        // this.fromSquareFeet();
        let dataFromSquareFeet = UnitConverter.fromSquareFeet({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromSquareFeet.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromSquareFeet.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromSquareFeet.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromSquareFeet.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromSquareFeet.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromSquareFeet.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromSquareFeet.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit === "Acres (ac)"
      ) {
        // this.fromAcre();
        let dataFromACre = UnitConverter.fromAcre({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromACre.acre;
        this.viewModel.areaUnitPropertiesObject.hectare = dataFromACre.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromACre.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromACre.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromACre.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromACre.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromACre.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit === "Hectares (ha)"
      ) {
        // this.fromHectare();
        let dataFromHectare = UnitConverter.fromHectare({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromHectare.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromHectare.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromHectare.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromHectare.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromHectare.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromHectare.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromHectare.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit === "Square Mile"
      ) {
        // this.fromSquareMile();
        let dataFromSquareMile = UnitConverter.fromSquareMile({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromSquareMile.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromSquareMile.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromSquareMile.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromSquareMile.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromSquareMile.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromSquareMile.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromSquareMile.squareMeter;
      } else if (
        this.viewModel.areaUnitPropertiesObject.targetUnit === "Square Yard"
      ) {
        // this.fromSquareYard();
        let dataFromSquareYard = UnitConverter.fromSquareYard({
          areaInput: this.viewModel.areaUnitPropertiesObject.areaInput,
        });
        this.viewModel.areaUnitPropertiesObject.acre = dataFromSquareYard.acre;
        this.viewModel.areaUnitPropertiesObject.hectare =
          dataFromSquareYard.hectare;
        this.viewModel.areaUnitPropertiesObject.squareYard =
          dataFromSquareYard.squareYard;
        this.viewModel.areaUnitPropertiesObject.squareFeet =
          dataFromSquareYard.squareFeet;
        this.viewModel.areaUnitPropertiesObject.squareMile =
          dataFromSquareYard.squareMile;
        this.viewModel.areaUnitPropertiesObject.squareKM =
          dataFromSquareYard.squareKM;
        this.viewModel.areaUnitPropertiesObject.squareMeter =
          dataFromSquareYard.squareMeter;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }
