import { Component } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { BankingViewModel } from "src/app/models/view/banking.viewmodel";
import { CommonService } from "src/app/services/common.service";
import { LogHandlerService } from "src/app/services/log-handler.service";
import Swal from "sweetalert2";
import * as UnitConverter from "../../../../../../../../UnitConverterBuisnessLogic";
@Component({
  selector: "app-discount",
  templateUrl: "./discount.component.html",
  styleUrls: ["./discount.component.scss"],
})
export class DiscountComponent extends BaseComponent<BankingViewModel> {
  bankingViewmodel: BankingViewModel = new BankingViewModel();

  constructor(commonService: CommonService, logService: LogHandlerService) {
    super(commonService, logService);
  }

  ngOnInit() {
    this._commonService.searchInput = "";
    this._commonService.layoutVM.showHideClass = "hide";
    this._commonService.showHideHomeButton = true;
    this._commonService.highlightCommonCategory = false;
    this._commonService.highlightOthersCategory = false;
    this._commonService.highlightEngineeringCategory = false;
    this._commonService.highlightBankingCategory = true;
    this._commonService.highlightHealthCategory = false;
  }

  // calculateDiscount() {
  //   if (
  //     this.bankingViewmodel.discountProperties.originalPrice &&
  //     this.bankingViewmodel.discountProperties.discountPercentage
  //   ) {
  //     this.bankingViewmodel.discountProperties.discountedPrice = +(
  //       this.bankingViewmodel.discountProperties.originalPrice *
  //       (1 - this.bankingViewmodel.discountProperties.discountPercentage / 100)
  //     ).toFixed(2);
  //     this.bankingViewmodel.discountProperties.savings = +(
  //       this.bankingViewmodel.discountProperties.originalPrice -
  //       this.bankingViewmodel.discountProperties.discountedPrice
  //     ).toFixed(2);
  //   }
  // }
  calculateDiscount() {
    let dataFromDOB = UnitConverter.calculateDiscount({
      originalPrice: this.bankingViewmodel.discountProperties.originalPrice,
      discountPercentage:
        this.bankingViewmodel.discountProperties.discountPercentage,
    });
    this.bankingViewmodel.discountProperties.discountedPrice =
      dataFromDOB?.discountedPrice;
    this.bankingViewmodel.discountProperties.savings = dataFromDOB?.savings;
  }
  resetValues() {
    this.bankingViewmodel.discountProperties.originalPrice = undefined;
    this.bankingViewmodel.discountProperties.discountPercentage = undefined;
    this.bankingViewmodel.discountProperties.discountedPrice = undefined;
    this.bankingViewmodel.discountProperties.savings = undefined;
  }
  ///copy toast function
  showCopiedToast = async () => {
    Swal.fire({
      text: "Copied",
      timer: 700,
      width: 200,
    });
  };
}
