  planeAngleUnitNames = [
    "Degree ()",
    "Radian (rad)",
    "Gradian (grad)",
    "Minute of Arc (MOA)",
    "Second of Arc (SOA)",
  ];

  targetUnit: string = this.planeAngleUnitNames[0];

  convertPlaneAngle() {
    if (this.viewModel.planeAngle != null) {
      if (this.targetUnit === "Degree ()") {
        // this.fromDegree();
        let data = UnitConverter.fromDegree({
          planeAngle: this.viewModel.planeAngle,
        });
        this.viewModel.radian = data.radian;
        this.viewModel.minuteOfArc = data.minuteOfArc;

        this.viewModel.secondOfArc = data.secondOfArc;

        this.viewModel.degree = data.degree;
        this.viewModel.gradian = data.gradian;
      } else if (this.targetUnit === "Radian (rad)") {
        // this.fromRadian();
        let data = UnitConverter.fromRadian({
          planeAngle: this.viewModel.planeAngle,
        });
        this.viewModel.radian = data.radian;
        this.viewModel.minuteOfArc = data.minuteOfArc;

        this.viewModel.secondOfArc = data.secondOfArc;

        this.viewModel.degree = data.degree;
        this.viewModel.gradian = data.gradian;
      } else if (this.targetUnit === "Gradian (grad)") {
        // this.fromGradian();
        let data = UnitConverter.fromGradian({
          planeAngle: this.viewModel.planeAngle,
        });
        this.viewModel.radian = data.radian;
        this.viewModel.minuteOfArc = data.minuteOfArc;

        this.viewModel.secondOfArc = data.secondOfArc;

        this.viewModel.degree = data.degree;
        this.viewModel.gradian = data.gradian;
      } else if (this.targetUnit === "Minute of Arc (MOA)") {
        // this.fromMinuteOfArc();
        let data = UnitConverter.fromMinuteOfArc({
          planeAngle: this.viewModel.planeAngle,
        });
        this.viewModel.radian = data.radian;
        this.viewModel.minuteOfArc = data.minuteOfArc;

        this.viewModel.secondOfArc = data.secondOfArc;

        this.viewModel.degree = data.degree;
        this.viewModel.gradian = data.gradian;
      } else if (this.targetUnit === "Second of Arc (SOA)") {
        // this.fromSecondOfArc();
        let data = UnitConverter.fromSecondOfArc({
          planeAngle: this.viewModel.planeAngle,
        });
        this.viewModel.radian = data.radian;
        this.viewModel.minuteOfArc = data.minuteOfArc;

        this.viewModel.secondOfArc = data.secondOfArc;

        this.viewModel.degree = data.degree;
        this.viewModel.gradian = data.gradian;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }

