  powerUnitNames = [
    "Watt (W)",
    "Kilowatt (kW)",
    "Horsepower (hp)",
    "Calorie per second (cal/s)",
    "BTU per hour (BTU/hr)",
    "Erg per second (erg/s)",
  ];

  targetUnit: string = this.powerUnitNames[0];

  // function to convert
  convertPower() {
    if (this.viewModel.power != null && this.viewModel.power > 0) {
      if (this.targetUnit === "Watt (W)") {
        // this.fromWatt();
        let data = UnitConverter.fromWatt({ power: this.viewModel.power });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else if (this.targetUnit === "Kilowatt (kW)") {
        // this.fromKilowatt();
        let data = UnitConverter.fromKilowatt({
          power: this.viewModel.power,
        });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else if (this.targetUnit === "Horsepower (hp)") {
        // this.fromHorsepower();
        let data = UnitConverter.fromHorsepower({
          power: this.viewModel.power,
        });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else if (this.targetUnit === "Calorie per second (cal/s)") {
        // this.fromCaloriePerSecond();
        let data = UnitConverter.fromCaloriePerSecond({
          power: this.viewModel.power,
        });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else if (this.targetUnit === "BTU per hour (BTU/hr)") {
        // this.fromBTUPerHour();
        let data = UnitConverter.fromBTUPerHour({
          power: this.viewModel.power,
        });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else if (this.targetUnit === "Erg per second (erg/s)") {
        // this.fromErgPerSecond();
        let data = UnitConverter.fromErgPerSecond({
          power: this.viewModel.power,
        });
        this.viewModel.kilowatt = data?.kilowatt;
        this.viewModel.horsepower = data?.horsepower;
        this.viewModel.caloriePerSecond = data?.caloriePerSecond;
        this.viewModel.btuperhour = data?.btuperhour;
        this.viewModel.ergPerSecond = data?.ergPerSecond;
        this.viewModel.watt = data?.watt;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }

