        this.viewModel.timeUnitPropertiesObject.minutes = data.minutes;
        this.viewModel.timeUnitPropertiesObject.hours = data.hours;
        this.viewModel.timeUnitPropertiesObject.days = data.days;
        this.viewModel.timeUnitPropertiesObject.weeks = data.weeks;
        this.viewModel.timeUnitPropertiesObject.months = data.months;
