  /**reseting all values to null:- means values will be nothing not even zero */
  resetValues() {
    this.viewModel.timeUnitPropertiesObject.timeInput = null;
    this.viewModel.timeUnitPropertiesObject.milliseconds = undefined;
    this.viewModel.timeUnitPropertiesObject.microseconds = undefined;
    this.viewModel.timeUnitPropertiesObject.nanoseconds = undefined;
    this.viewModel.timeUnitPropertiesObject.seconds = undefined;
    this.viewModel.timeUnitPropertiesObject.minutes = undefined;
    this.viewModel.timeUnitPropertiesObject.hours = undefined;
    this.viewModel.timeUnitPropertiesObject.days = undefined;
    this.viewModel.timeUnitPropertiesObject.weeks = undefined;
    this.viewModel.timeUnitPropertiesObject.years = undefined;
    this.viewModel.timeUnitPropertiesObject.months = undefined;
    this.viewModel.timeUnitPropertiesObject.decades = undefined;
  }

  setTargetUnit(target: string) {
    this.viewModel.timeUnitPropertiesObject.targetUnit = target;
    this.convertTime();
  }
