  resetStorageValues() {
    this.viewModel.bit = undefined;
    this.viewModel.byte = undefined;
    this.viewModel.kilobit = undefined;
    this.viewModel.kilobyte = undefined;
    this.viewModel.megabit = undefined;
    this.viewModel.megabyte = undefined;
    this.viewModel.gigabit = undefined;
    this.viewModel.gigabyte = undefined;
    this.viewModel.terabit = undefined;
    this.viewModel.terabyte = undefined;
    this.viewModel.petabit = undefined;
    this.viewModel.petabyte = undefined;
    this.viewModel.exabit = undefined;
    this.viewModel.exabyte = undefined;
    this.viewModel.storage = null;
  }
  setTargetUnit(target: string) {
    this.targetStorageUnit = target;
    this.convertStorageUnit();
  }
