// src/temperatureConverter.ts
function fromCelsius({
  temperature,
  celsius,
  fahrenheit,
  kelvin
}) {
  if (temperature != null) {
    celsius = Number(temperature);
    fahrenheit = Number(temperature * 9 / 5 + 32);
    kelvin = Number(temperature + 273.15);
  }
  return {
    temperature,
    celsius,
    fahrenheit,
    kelvin
  };
}
function fromFahrenheit({
  temperature,
  celsius,
  fahrenheit,
  kelvin
}) {
  if (temperature != null) {
    fahrenheit = Number(temperature);
    celsius = Number((temperature - 32) * 5 / 9);
    kelvin = Number((temperature - 32) * 5 / 9 + 273.15);
  }
  return {
    temperature,
    celsius,
    fahrenheit,
    kelvin
  };
}
function fromKelvin({
  temperature,
  celsius,
  fahrenheit,
  kelvin
}) {
  if (temperature != null) {
    kelvin = Number(temperature);
    celsius = Number(temperature - 273.15);
    fahrenheit = Number((temperature - 273.15) * 9 / 5 + 32);
  }
  return {
    temperature,
    celsius,
    fahrenheit,
    kelvin
  };
}

