  calculateBMI() {
    // ///////// npm fucnntion used
    let datafromBMI = UnitConverter.calculateBMI({
      weightInKG: this.viewModel.bmiUnitPropertiesObject.weight,
      heightInCM: this.viewModel.bmiUnitPropertiesObject.height,
    });
    if (datafromBMI != undefined) {
      this.viewModel.bmiUnitPropertiesObject.bmi = datafromBMI.bmi;
      this.viewModel.bmiUnitPropertiesObject.bmiCategory =
        datafromBMI.bmiCategory;
    }
    // npm function ended
  }
