  speedUnitNames = [
    "Meters per second (m/s)",
    "Kilometers per second (km/s)",
    "Miles per hour (mph)",
    "Feet per second (ft/s)",
    "Knots (kn)",
    "Kilometer per Hour",
  ];

  targetUnit: string = this.speedUnitNames[0];
  convertSpeed() {
    if (this.viewModel.speed != null && this.viewModel.speed > 0) {
      if (this.targetUnit === "Meters per second (m/s)") {
        // this.fromMeterPerSecond();
        let data = UnitConverter.fromMeterPerSecond({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else if (this.targetUnit === "Kilometers per second (km/s)") {
        // this.fromKilometerPerSecond();
        let data = UnitConverter.fromKilometerPerSecond({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else if (this.targetUnit === "Miles per hour (mph)") {
        // this.fromMilesPerHour();
        let data = UnitConverter.fromMilesPerHour({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else if (this.targetUnit === "Feet per second (ft/s)") {
        // this.fromFeetPerSecond();
        let data = UnitConverter.fromFeetPerSecond({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else if (this.targetUnit === "Knots (kn)") {
        // this.fromKnots();
        let data = UnitConverter.fromKnots({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else if (this.targetUnit === "Kilometer per Hour") {
        // this.fromKilometerPerHour();
        let data = UnitConverter.fromKilometerPerHour({
          speed: this.viewModel.speed,
        });
        this.viewModel.meterPerSecond = data?.meterPerSecond;
        this.viewModel.kilometerPerSecond = data?.kilometerPerSecond;
        this.viewModel.milesPerHour = data?.milesPerHour;
        this.viewModel.feetPerSecond = data?.feetPerSecond;
        this.viewModel.knots = data?.knots;
        this.viewModel.kilometerperhour = data?.kilometerperhour;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }
