  pressureUnitNames = [
    "Pascal (Pa)",
    "Kilopascal (kPa)",
    "Bar",
    "Atmosphere (atm)",
    "Pound per Square Inch (psi)",
    "Torr",
  ];

  targetUnit: string = this.pressureUnitNames[0];
  convertPressure() {
    if (this.viewModel.pressure != null && this.viewModel.pressure > 0) {
      if (this.targetUnit === "Pascal (Pa)") {
        // this.fromPascal();
        let data = UnitConverter.fromPascal({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else if (this.targetUnit === "Kilopascal (kPa)") {
        // this.fromKilopascal();
        let data = UnitConverter.fromKilopascal({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else if (this.targetUnit === "Bar") {
        // this.fromBar();
        let data = UnitConverter.fromBar({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else if (this.targetUnit === "Atmosphere (atm)") {
        // this.fromAtmosphere();
        let data = UnitConverter.fromAtmosphere({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else if (this.targetUnit === "Pound per Square Inch (psi)") {
        // this.fromPsi();
        let data = UnitConverter.fromPsi({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else if (this.targetUnit === "Torr") {
        // this.fromTorr();
        let data = UnitConverter.fromTorr({
          pressure: this.viewModel.pressure,
        });
        this.viewModel.kilopascal = data?.kilopascal;
        this.viewModel.bar = data?.bar;
        this.viewModel.atmosphere = data?.atmosphere;
        this.viewModel.psi = data?.psi;
        this.viewModel.torr = data?.torr;
        this.viewModel.pascal = data?.pascal;
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }
