  //     if (this.healthViewModel.bmrUnitPropertiesObject.gender === "Male") {
  //       this.healthViewModel.bmrUnitPropertiesObject.bmr = +(
  //         10 * this.healthViewModel.bmrUnitPropertiesObject.weight +
  //         6.25 * (heightInMeters * 100) -
  //         5 * this.healthViewModel.bmrUnitPropertiesObject.age +
