  calculateBMR() {
    let dataFromBMR = UnitConverter.calculateBMR({
      weightInKG: this.healthViewModel.bmrUnitPropertiesObject.weight,
      heightInCM: this.healthViewModel.bmrUnitPropertiesObject.height,
      age: this.healthViewModel.bmrUnitPropertiesObject.age,
      gender: this.healthViewModel.bmrUnitPropertiesObject.gender,
    });
    if (dataFromBMR) {
      this.healthViewModel.bmrUnitPropertiesObject.bmr = dataFromBMR;
    }
  }
  selectGender(gender: string) {
    this.healthViewModel.bmrUnitPropertiesObject.gender = gender;
    this.calculateBMR();
  }

  resetBMRValues() {
    this.healthViewModel.bmrUnitPropertiesObject.weight = undefined;
    this.healthViewModel.bmrUnitPropertiesObject.height = undefined;
    this.healthViewModel.bmrUnitPropertiesObject.age = undefined;
    this.healthViewModel.bmrUnitPropertiesObject.gender = undefined;
    this.healthViewModel.bmrUnitPropertiesObject.bmr = undefined;
  }
