import { BaseViewModel } from "../internal/base.viewmodel";
import {
  BMIUnitProperties,
  BMRUnitProperties,
  WaterIntakeProperties,
} from "../internal/calculator-units";
import { DummyTeacherSM } from "../service/v1/dummy-teacher-s-m";

export class HealthViewModel implements BaseViewModel {
  PageTitle: string = "";
  AddEditModalTitle: string = "";
  singleTeacher!: DummyTeacherSM;
  filteredHealthConverters: any[] = [];
  healthConverters: FilteredHealthConverters[] = [];
  /**
   * objects of Health Category Units from class file calculator-units.ts
   */
  bmiUnitPropertiesObject = new BMIUnitProperties();
  bmrUnitPropertiesObject = new BMRUnitProperties();
  waterIntakePropertiesObject = new WaterIntakeProperties();

  isBmr!: boolean;
  isWaterIntake!: boolean;
  isBmi!: boolean;
  isSelected: boolean = false;
  selectedItem: any;
  defaultSelectedItem: any;
}
export class FilteredHealthConverters {
  title: string = "";
  imgSrc: string = "";
  isFavorite!: boolean;
  isSelected!: boolean;
  router: string = "";
  searchArrayFromConverters: string[] = [];
}
